import { useState } from "react";
import { useForm } from "react-hook-form";

const AuthorizationForm = (props) => {
    const [statePassword, changeStatePassword] = useState(false);
    const [errorAuth, setError] = useState(null);
    const [rememberChecked, setRememberChecked] = useState(false);

    const {
        register,
        formState: { errors },
        handleSubmit,
        reset
    } = useForm({
        mode: 'onBlur'
    });

    const onSubmit = (data) => {
        const users = JSON.parse(localStorage.getItem('users'));
        users.map(user => {
            if (user.login === data.login && user.password === data.password) {
                props.handleLogIn({ ...user, isLogged: true });
                setError(false);
            } else {
                setError(true);
            }
            return user;
        });


        reset();
    }

    return (
        <form className='authorization-form' onSubmit={handleSubmit(onSubmit)}>
            {errorAuth && <div className='error-message'>{errorAuth && '   .'}</div>}
            <label>
                
                <input
                    type='text'
                    placeholder=' '
                    className={errors.login !== undefined ? 'error' : ''}
                    {...register('login', {
                        required: '    .',
                        minLength: {
                            value: 3,
                            message: '    3  16 .'
                        },
                        maxLength: {
                            value: 16,
                            message: '    3  16 .'
                        },
                        pattern: {
                            value: /[a-zA-Z0-9]/,
                            message: '  .'
                        }
                    })}
                />
            </label>
            <div className='error-message'>
                {errors?.login && <p>{errors?.login?.message || 'Error!'}</p>}
            </div>
            <label className='password'>
                
                <input
                    type={statePassword ? 'text' : 'password'}
                    className={`${statePassword ? 'active' : ''} ${errors.password !== undefined ? 'error' : ''}`}
                    placeholder=' '
                    {...register('password', {
                        required: '    .',
                        minLength: {
                            value: 6,
                            message: '    6  16 .'
                        },
                        maxLength: {
                            value: 16,
                            message: '    6  16 .'
                        },
                        pattern: {
                            value: /(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])/,
                            message: '  '
                        }
                    })}
                />
                <p className={statePassword ? 'active' : ''} onClick={() => changeStatePassword(!statePassword)}></p>
            </label>
            <div className='error-message'>
                {errors?.password && <p>{errors?.password?.message || 'Error!'}</p>}
            </div>
            <div className='remember'>
                <div>
                    <input type='checkbox' name='remember' checked={rememberChecked} onChange={() => {}} />
                    <label htmlFor='remember' onClick={() => setRememberChecked(!rememberChecked)}></label>
                </div>
                <button> ?</button>
            </div>
            <input type='submit' value='' />
        </form>
    )
}

export default AuthorizationForm;